import styled from "styled-components";
import Description from "./descriptionDesktop";
import PhoneDesktop from "./phoneDesktop";

const StyledIntro = styled.section`
  display: flex;
  position: relative;
  overflow: hidden;

  padding-top: 190px;
  padding-bottom: 120px;
  z-index: 2;
  
  background-color: #000f67;

  &::after {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    
    filter: blur(40px);

    background: radial-gradient(
        60.73% 118.13% at 101.88% 105.38%,
        #00aee8 0%,
        rgba(0, 174, 232, 0.77) 22.22%,
        rgba(0, 174, 232, 0.4) 58.01%,
        rgba(0, 174, 232, 0) 82.68%,
        rgba(0, 174, 232, 0) 100%
      ),
      radial-gradient(
        26.38% 39.48% at 58.18% 54.77%,
        rgba(0, 174, 232, 0.7128) 0%,
        rgba(0, 174, 232, 0.5456) 36.81%,
        rgba(0, 174, 232, 0.2112) 83.7%,
        rgba(0, 174, 232, 0) 100%
      ),
      radial-gradient(
        100.72% 136.21% at -1.61% -13.56%,
        rgba(98, 94, 102, 0.84) 0%,
        #7400e8 0.01%,
        rgba(116, 0, 232, 0) 100%
      ),
      radial-gradient(
        48.1% 113.48% at 24.44% 9.59%,
        #ff7cda 0%,
        rgba(255, 124, 218, 0.7) 18.29%,
        rgba(255, 124, 218, 0.43) 44.79%,
        rgba(255, 124, 218, 0) 69.45%
      );
      z-index: 1;
  }

  &::after {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    background-color: #000f67;
    z-index: 0;
  }
  
  & * {
    z-index: 100;
  }

`;

const IntroInner = styled.div`
  margin: 0 15%;
  width: 100%;
  display: flex;
  gap: 20px;
  justify-content: space-between;
  
  @media (max-width: 1200px) {
    width: calc(100vw - 10%);
    margin: 0 10%;
    flex-direction: column;
    gap: 60px;
  }
`;

const IntroDesktop = ({language}) => {
  return (
    <StyledIntro id={"intro"}>
      <IntroInner>
        <Description language={language} />
        <PhoneDesktop />
      </IntroInner>
    </StyledIntro>
  );
};

export default IntroDesktop;
