import { Title1 } from "../../assets/styles/component";
import styled from "styled-components";
import Button from "../../assets/UI/ButtonDesktop";
import PlayVideo from "../../assets/images/playVideo.svg";
import Tag from "./tag";
import { EngLoc } from "../../assets/constants/localizations";

const SDescription = styled.div`
  max-width: 900px;
  text-align: left;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  @media (max-width: 1200px) {
    gap: 40px;
  }
`;

const IntroTitle = styled(Title1)`
  font-size: clamp(62px, 3vw, 73px);
  line-height: 1.4;
  margin-bottom: 15px;
`;

const BorderedText = styled.span`
  background: linear-gradient(
    92.84deg,
    rgba(255, 255, 255, 0.05) 2.76%,
    rgba(255, 255, 255, 0) 108.78%
  );
  border: 1.05284px solid rgba(255, 255, 255, 0.2);
  border-radius: clamp(18px, 2vw, 37px);
  padding: 0.5% 2% 2.5% 2%;
  margin-left: -10px;
`;

const ButtonDiv = styled.div`
  display: flex;
  gap: 50px;

  & img {
    aspect-ratio: 60 / 60;
  }
`;

const TagCloud = styled.div`
  display: flex;
  max-width: 570px;
  gap: 8px;
  flex-wrap: wrap;
`;

const Description = ({ language }) => {
  const Tags = [
    language === "Ru" ? "Реклама в Telegram" : EngLoc.intro.tags.telegram,
    language === "Ru" ? "Реклама в Вконтакте" : EngLoc.intro.tags.vk,
    language === "Ru"
      ? "Реклама в Одноклассниках"
      : EngLoc.intro.tags.odnoclassniki,
    language === "Ru" ? "Видео-реклама" : EngLoc.intro.tags.Video_Ads,
    language === "Ru" ? "Аналитика" : EngLoc.intro.tags.Analytics,
    language === "Ru" ? "olv" : EngLoc.intro.tags.olv,
    language === "Ru" ? "TG Ads" : EngLoc.intro.tags.TG_Ads,
    language === "Ru" ? "инстрим" : EngLoc.intro.tags.instream,
  ];

  return (
    <SDescription>
      <IntroTitle weight="300">
        {language === "Ru"
          ? "Технологические решения для вашего"
          : EngLoc.intro.title}
        <BorderedText>
          {language === "Ru" ? "маркетинга" : EngLoc.intro.title_span}
        </BorderedText>
      </IntroTitle>
      <TagCloud>
        {Tags.map((tag) => (
          <Tag key={Math.random()}>{tag}</Tag>
        ))}
      </TagCloud>
      <ButtonDiv>
        <Button link={true}>
          {language === "Ru" ? "Быстрая заявка" : EngLoc.buttons.request}
        </Button>
        <Button type="gray">
          {language === "Ru" ? "Смотреть видео" : EngLoc.buttons.video}{" "}
          <img width="60" height="60" loading="lazy" src={PlayVideo} />
        </Button>
      </ButtonDiv>
    </SDescription>
  );
};

export default Description;
